/**
 * Admin styles

 * This file is used to apply styles to the admin panel
 * It is enqueued in inc/enqueue.php
 */
/* --- Plugin / ACF --- */
body .acf-block-fields, body .acf-fields {
  font-weight: 400;
}
body .acf-tab-wrap {
  overflow: initial;
}
body .acf-flexible-content .layout {
  transition: margin 0.2s ease-out;
}
body .acf-flexible-content .layout:not(.-collapsed):not(:first-child) {
  margin: 50px 0 0;
}
body .acf-repeater > table > thead > tr > th {
  font-weight: 700;
}
body .acf-repeater .acf-row:nth-child(even) > .acf-row-handle {
  background-color: #ececec;
}
body .acf-repeater .acf-row:nth-child(even) > .acf-fields,
body .acf-repeater .acf-row:nth-child(even) > .acf-field {
  background-color: #fbfbfb;
}
body .acf-force-duplicate .acf-table > tbody > tr > th,
body .acf-force-duplicate .acf-table > tbody > tr > td,
body .acf-force-duplicate .acf-table > thead > tr > th,
body .acf-force-duplicate .acf-table > thead > tr > td {
  padding: 15px 8px;
}
body .acf-force-duplicate .acf-row-handle .acf-icon.-plus {
  top: 0;
}
body .acf-force-duplicate .acf-row-handle .acf-icon.-minus {
  top: 44px;
}
body .acf-force-duplicate .acf-row-handle .acf-icon.-duplicate {
  top: 22px;
}
body .acf-force-duplicate .acf-row:hover .acf-row-handle .acf-icon.-duplicate,
body .acf-force-duplicate .acf-row.-hover .acf-row-handle .acf-icon.-duplicate {
  display: block;
}
body .acf-field-message.acf-warning {
  background: #fff;
  border: 1px solid #ccd0d4 !important;
  border-left-color: #ffb900 !important;
  border-left-width: 4px !important;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.0392156863);
  margin: 5px 12px 15px;
  padding: 1px 12px;
}
body .acf-field-message.acf-warning .acf-label {
  display: none;
}
body .acf-field-message.acf-warning p {
  margin: 0.5em 0;
  padding: 2px;
}

body .wp-block.acf-block-component:not(.is-selected) > div:empty::before,
body .wp-block.acf-block-component:not(.is-selected) .acf-block-preview:empty::before {
  background: #f3f3f3;
  color: #bbb;
  content: "This block is empty. Click to add content";
  display: block;
  padding: 20px 10px;
  text-align: center;
}